@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Poppins:wght@300&family=Staatliches&display=swap');
body {
  background: var(--color-bg);
  font-family: 'Poppins', sans-serif;
  color: var(--color-white);
  line-height: 1.7;;
  background-image: url(../src/Assets/Images/background.jpg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: 0;
  list-style: none;
  text-decoration: none;
}
:root {
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: #4d94ff;
  --color-white: #fff;
  --color-light:	#808080;
  --smoothTrans: all 400ms ease;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

/* Generic Styling */
.container{
  width:var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  height: 100vh;
}
section > h2, section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}
.text-light{
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--smoothTrans);
}

a:hover {
  color: var(--color-white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border:1px solid var(--color-primary);
  transition: var(--smoothTrans);
}
.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary  {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* other devices (tablets) */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);

  }
  section {
    margin-top: 6rem;

  }
}

/* other devices (phones) */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);

  }
  section > h2 {
    margin-top: 2rem;
  }
}